
@font-face {
  font-family: 'iconfont';
  /* project id 32461 */
  src: url('//at.alicdn.com/t/font_cdfr3lavi5vfs9k9.eot');
  src: url('//at.alicdn.com/t/font_cdfr3lavi5vfs9k9.eot?#iefix') format('embedded-opentype'), url('//at.alicdn.com/t/font_cdfr3lavi5vfs9k9.woff') format('woff'), url('//at.alicdn.com/t/font_cdfr3lavi5vfs9k9.ttf') format('truetype'), url('//at.alicdn.com/t/font_cdfr3lavi5vfs9k9.svg#iconfont') format('svg');
}
.close {
  font-family: "iconfont";
  font-size: 24px;
  font-style: normal;
}
.decrease {
  font-family: "iconfont";
  font-size: 24px;
  font-style: normal;
  color: #00a0dc;
}
.increase {
  font-family: "iconfont";
  font-size: 24px;
  font-style: normal;
  color: #00a0dc;
}
.header {
  position: relative;
  padding: 24px 12px 18px 24px;
  background: rgba(7, 17, 27, 0.5);
  overflow: hidden;
  font-size: 0;
}
.header .background {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: -1;
  -webkit-filter: blur(10px);
  filter: blur(10px);
}
.header .avatar {
  width: 64px;
  height: 64px;
  display: inline-block;
  vertical-align: top;
}
.header .desc {
  margin-left: 16px;
  font-size: 16px;
  display: inline-block;
  color: #fff;
}
.header .desc .title {
  margin: 0 2px 2px 0;
}
.header .desc .title .brand {
  display: inline-block;
  vertical-align: top;
  width: 30px;
  height: 18px;
  background-size: 100% 100%;
  background-image: url("../../../resource/img/brand@2x.png");
}
.header .desc .description {
  font-size: 14px;
  line-height: 20px;
}
.header .desc .support {
  font-size: 14px;
  line-height: 20px;
}
.header .desc .support .icon {
  display: inline-block;
  width: 12px;
  height: 12px;
  background-size: 100% 100%;
}
.header .desc .support .icon.decrease {
  background-image: url("../../../resource/img/decrease_1@2x.png");
}
.header .desc .support .icon.discount {
  background-image: url("../../../resource/img/discount_1@2x.png");
}
.header .desc .support .icon.guarantee {
  background-image: url("../../../resource/img/guarantee_1@2x.png");
}
.header .desc .support .icon.invoice {
  background-image: url("../../../resource/img/invoice_1@2x.png");
}
.header .desc .support .icon.special {
  background-image: url("../../../resource/img/special_1@2x.png");
}
.header .desc .supports-count {
  position: absolute;
  right: 12px;
  bottom: 14px;
  line-height: 24px;
  padding: 0 8px;
  border-radius: 14px;
  text-align: center;
  font-size: 10px;
  background: rgba(255, 255, 255, 0.2);
}
.notice {
  position: relative;
  height: 28px;
  line-height: 28px;
  padding: 0 22px 0 12px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  background: rgba(7, 17, 27, 0.2);
  padding: 0 12px;
}
.notice .notice-arrow {
  display: inline-block;
  width: 12px;
  height: 12px;
  position: absolute;
  font-size: 10px;
  right: 6px;
  line-height: 12px;
  top: 8px;
}
.notice .icon {
  vertical-align: middle;
  display: inline-block;
  width: 16px;
  height: 12px;
  background-size: 100% 100%;
  background-image: url("../../../resource/img/bulletin@2x.png");
}
.notice .notice-font {
  font-size: 12px;
}
.detail-wrapper {
  width: 100%;
  height: 100%;
  position: fixed;
  text-align: center;
  top: 0;
  left: 0;
  z-index: 100;
  transition: all 0.5s;
  background: rgba(7, 17, 27, 0.8);
}
.detail-wrapper.fade-enter-active,
.detail-wrapper.fade-leave-active {
  transition: opacity 0.5s;
}
.detail-wrapper.fade-enter,
.detail-wrapper.fade-leave-active {
  opacity: 0;
}
.detail-wrapper .detail-title {
  color: #fff;
  font-size: 18px;
  font-weight: bold;
  margin: 64px auto 16px;
}
.detail-wrapper .discount-info {
  width: 80%;
  margin: 0 auto;
  margin-bottom: 24px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
.detail-wrapper .discount-info .line {
  position: relative;
  top: -12px;
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  border-bottom: 1px solid #eee;
}
.detail-wrapper .discount-info .text {
  padding: 6px;
  font-size: 16px;
  color: #fff;
}
.detail-wrapper .supports {
  width: 80%;
  margin: 28px auto;
  text-align: left;
}
.detail-wrapper .supports .support-item {
  color: #fff;
  font-size: 14px;
  line-height: 20px;
}
.detail-wrapper .supports .support-item .icon {
  vertical-align: top;
  display: inline-block;
  width: 16px;
  height: 16px;
  background-size: 100% 100%;
}
.detail-wrapper .supports .support-item .icon.decrease {
  background-image: url("../../../resource/img/decrease_1@2x.png");
}
.detail-wrapper .supports .support-item .icon.discount {
  background-image: url("../../../resource/img/discount_1@2x.png");
}
.detail-wrapper .supports .support-item .icon.guarantee {
  background-image: url("../../../resource/img/guarantee_1@2x.png");
}
.detail-wrapper .supports .support-item .icon.invoice {
  background-image: url("../../../resource/img/invoice_1@2x.png");
}
.detail-wrapper .supports .support-item .icon.special {
  background-image: url("../../../resource/img/special_1@2x.png");
}
.detail-wrapper .close {
  display: inline-block;
  position: relative;
  width: 32px;
  height: 32px;
  margin: -64px auto 0 auto;
  clear: both;
  font-size: 32px;
  color: #fff;
}



