
@font-face {
  font-family: 'iconfont';
  /* project id 32461 */
  src: url('//at.alicdn.com/t/font_cdfr3lavi5vfs9k9.eot');
  src: url('//at.alicdn.com/t/font_cdfr3lavi5vfs9k9.eot?#iefix') format('embedded-opentype'), url('//at.alicdn.com/t/font_cdfr3lavi5vfs9k9.woff') format('woff'), url('//at.alicdn.com/t/font_cdfr3lavi5vfs9k9.ttf') format('truetype'), url('//at.alicdn.com/t/font_cdfr3lavi5vfs9k9.svg#iconfont') format('svg');
}
.close {
  font-family: "iconfont";
  font-size: 24px;
  font-style: normal;
}
.decrease {
  font-family: "iconfont";
  font-size: 24px;
  font-style: normal;
  color: #00a0dc;
}
.increase {
  font-family: "iconfont";
  font-size: 24px;
  font-style: normal;
  color: #00a0dc;
}
.star .star-item {
  display: inline-block;
}
.star-48 .star-item {
  width: 20px;
  height: 20px;
  margin-right: 22px;
  background-size: 100% 100%;
}
.star-48 .star-item.ON {
  background-image: url("../../resource/img/star48_on@2x.png");
}
.star-48 .star-item.Half {
  background-image: url("../../resource/img/star48_half@2x.png");
}
.star-48 .star-item.Off {
  background-image: url("../../resource/img/star48_off@2x.png");
}
.star-48 .star-item:last-child {
  margin-right: 0;
}
.star-36 .star-item {
  width: 15px;
  height: 15px;
  margin-right: 6px;
  background-size: 100% 100%;
}
.star-36 .star-item:last-child {
  margin-right: 0;
}
.star-36 .star-item.ON {
  background-image: url("../../resource/img/star36_on@2x.png");
}
.star-36 .star-item.half {
  background-image: url("../../resource/img/star36_half@2x.png");
}
.star-36 .star-item.off {
  background-image: url("../../resource/img/star36_off@2x.png");
}



