
@font-face {
  font-family: 'iconfont';
  /* project id 32461 */
  src: url('//at.alicdn.com/t/font_cdfr3lavi5vfs9k9.eot');
  src: url('//at.alicdn.com/t/font_cdfr3lavi5vfs9k9.eot?#iefix') format('embedded-opentype'), url('//at.alicdn.com/t/font_cdfr3lavi5vfs9k9.woff') format('woff'), url('//at.alicdn.com/t/font_cdfr3lavi5vfs9k9.ttf') format('truetype'), url('//at.alicdn.com/t/font_cdfr3lavi5vfs9k9.svg#iconfont') format('svg');
}
.close {
  font-family: "iconfont";
  font-size: 24px;
  font-style: normal;
}
.decrease {
  font-family: "iconfont";
  font-size: 24px;
  font-style: normal;
  color: #00a0dc;
}
.increase {
  font-family: "iconfont";
  font-size: 24px;
  font-style: normal;
  color: #00a0dc;
}
.detail-wrap {
  background: #ffffff;
  width: 100%;
  position: fixed;
  top: 0;
  bottom: 48px;
  left: 0;
  z-index: 97;
  transition: all .3s linear;
  -webkit-transform: translate3d(0, 0, 0);
          transform: translate3d(0, 0, 0);
}
.detail-top {
  position: relative;
}
.detail-top:after {
  display: block;
  position: absolute;
  left: 0;
  bottom: 0;
  border-top: 1px solid rgba(7, 17, 27, 0.1);
  content: ' ';
  width: 100%;
}
.detail {
  transition: all .3s linear;
  -webkit-transform: translate3d(0, 0, 0);
          transform: translate3d(0, 0, 0);
}
.detail .detail-header {
  position: relative;
  width: 100%;
  height: 0;
  padding-top: 100%;
}
.detail .detail-header img {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
.detail .detail-header .arrow-left {
  position: absolute;
  top: 0;
  left: 0;
  color: #fff;
  padding: 10px;
  font-weight: bold;
}
.detail .detail-info {
  position: relative;
  padding: 18px;
}
.detail .detail-info .info-title {
  line-height: 14px;
  margin-bottom: 8px;
  font-size: 14px;
  font-weight: 700;
  color: #07111b;
}
.detail .detail-info .info-desc {
  margin-bottom: 18px;
  line-height: 10px;
  height: 10px;
  font-size: 0;
}
.detail .detail-info .info-desc span {
  font-size: 10px;
  color: #93999f;
}
.detail .detail-info .info-desc .rating {
  margin-left: 15px;
}
.detail .detail-info .price {
  font-size: 14px;
}
.detail .detail-info .price .now {
  color: red;
  margin-right: 10px;
}
.detail .detail-info .cartControl {
  position: absolute;
  right: 12px;
  bottom: 12px;
}
.detail .detail-info .addCart {
  position: absolute;
  right: 12px;
  bottom: 12px;
  border-radius: 12px;
  padding: 0 12px;
  height: 25px;
  line-height: 25px;
  font-size: 12px;
  background: #00a0dc;
  color: #fff;
}
.swipe-enter,
.swipe-leave {
  -webkit-transform: translate3d(100%, 0, 0);
          transform: translate3d(100%, 0, 0);
}
.swipe-leave-active {
  -webkit-transform: translate3d(100%, 0, 0);
          transform: translate3d(100%, 0, 0);
}
.info {
  margin-top: 18px;
  border-top: 1px solid rgba(7, 17, 27, 0.1);
  border-bottom: 1px solid rgba(7, 17, 27, 0.1);
  padding: 18px;
}
.info .title {
  line-height: 14px;
  margin-bottom: 6px;
  font-size: 14px;
  color: #07111b;
}
.info .text {
  line-height: 24px;
  padding: 0 8px;
  font-size: 12px;
  color: #4d555d;
}
.rating .title {
  line-height: 14px;
  margin-left: 18px;
  font-size: 14px;
  color: #07111b;
}
.rating .rating-type {
  padding: 18px 0;
  margin: 0 18px;
  font-size: 0;
  position: relative;
}
.rating .rating-type:after {
  display: block;
  position: absolute;
  left: 0;
  bottom: 0;
  border-top: 1px solid rgba(7, 17, 27, 0.1);
  content: ' ';
  width: 100%;
}
.rating .rating-type .block {
  display: inline-block;
  padding: 8px 12px;
  margin-right: 8px;
  line-height: 16px;
  border-radius: 1px;
  font-size: 12px;
  color: #4d555d;
}
.rating .rating-type .block.positive {
  background: rgba(0, 160, 220, 0.2);
}
.rating .rating-type .block.positive.active {
  background: #00a0dc;
  color: #fff;
}
.rating .rating-type .block.negative {
  background: rgba(77, 85, 93, 0.2);
}
.rating .rating-type .block.negative.active {
  background: #4d555d;
  color: #fff;
}
.rating .switch {
  padding: 12px 18px;
  line-height: 24px;
  border-bottom: 1px solid rgba(7, 17, 27, 0.1);
  color: #93999f;
  font-size: 0;
}
.rating .switch .icon-check_circle {
  font-family: "iconfont";
  font-size: 24px;
  font-style: normal;
  color: #eee;
}
.rating .switch.on .icon-check_circle {
  color: #00c850;
}
.rating .switch .text {
  display: inline-block;
  vertical-align: top;
  font-size: 12px;
}
.rating .rating-wrapper {
  padding: 0 18px;
}
.rating .rating-wrapper .each-rating {
  padding: 16px 0;
  position: relative;
}
.rating .rating-wrapper .each-rating .user {
  position: absolute;
  right: 0;
  top: 16px;
  line-height: 12px;
  font-size: 10px;
  color: #93999f;
}
.rating .rating-wrapper .each-rating .time {
  margin-bottom: 6px;
  line-height: 12px;
  font-size: 10px;
  color: #93999f;
}
.rating .rating-wrapper .each-rating .text {
  font-size: 12px;
}
.rating .rating-wrapper .each-rating .text .icon {
  font-family: "iconfont";
  color: #93999f;
  font-size: 12px;
}
.rating .rating-wrapper .each-rating .text .icon.like {
  color: #00a0dc;
}



