
@font-face {
  font-family: 'iconfont';
  /* project id 32461 */
  src: url('//at.alicdn.com/t/font_cdfr3lavi5vfs9k9.eot');
  src: url('//at.alicdn.com/t/font_cdfr3lavi5vfs9k9.eot?#iefix') format('embedded-opentype'), url('//at.alicdn.com/t/font_cdfr3lavi5vfs9k9.woff') format('woff'), url('//at.alicdn.com/t/font_cdfr3lavi5vfs9k9.ttf') format('truetype'), url('//at.alicdn.com/t/font_cdfr3lavi5vfs9k9.svg#iconfont') format('svg');
}
.close {
  font-family: "iconfont";
  font-size: 24px;
  font-style: normal;
}
.decrease {
  font-family: "iconfont";
  font-size: 24px;
  font-style: normal;
  color: #00a0dc;
}
.increase {
  font-family: "iconfont";
  font-size: 24px;
  font-style: normal;
  color: #00a0dc;
}
.shopcart {
  width: 100%;
  position: fixed;
  bottom: 0;
  left: 0;
  z-index: 99;
  height: 48px;
  background: #141d27;
}
.shopcart .content {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  /*padding: 7px 0;*/
}
.shopcart .content .content-left {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
}
.shopcart .content .content-left .icon {
  position: relative;
  display: inline-block;
  top: -7px;
}
.shopcart .content .content-left .icon .circle-big {
  display: inline-block;
  width: 44px;
  padding: 6px;
  height: 44px;
  border-radius: 50%;
  background: #141d27;
}
.shopcart .content .content-left .icon .circle-big .circle-inner {
  display: inline-block;
  width: 44px;
  height: 44px;
  vertical-align: middle;
  line-height: 44px;
  border-radius: 50%;
  background: rgba(255, 255, 255, 0.2);
  color: rgba(255, 255, 255, 0.4);
  font-family: "iconfont";
  font-size: 24px;
  font-style: normal;
  text-align: center;
}
.shopcart .content .content-left .icon .circle-big .circle-inner.highLight {
  background: #00a0dc;
  color: #fff;
}
.shopcart .content .content-left .products-num {
  position: absolute;
  top: 0;
  right: 0;
  border-radius: 15px;
  padding: 0 6px;
  color: #fff;
  font-size: 9px;
  line-height: 16px;
  background: red;
  text-align: center;
}
.shopcart .content .content-left .text {
  display: inline-block;
  height: 30px;
  line-height: 30px;
}
.shopcart .content .content-left .text .price {
  display: inline-block;
  padding-right: 15px;
  height: 100%;
  color: rgba(255, 255, 255, 0.1);
  font-size: 16px;
  font-weight: bold;
  border-right: 1px solid rgba(255, 255, 255, 0.1);
}
.shopcart .content .content-left .text .price.highLight {
  color: #fff;
}
.shopcart .content .content-left .text .delivery {
  margin-left: 10px;
}
.shopcart .btn {
  -webkit-box-flex: 0;
      -ms-flex: 0 0 105px;
          flex: 0 0 105px;
  height: 30px;
  padding: 9px 6px;
  line-height: 30px;
  -ms-grid-column-align: center;
      justify-items: center;
  background: #2b333b;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  text-align: center;
  color: rgba(255, 255, 255, 0.4);
  font-size: 12px;
}
.shopcart .btn.not-enough {
  background: #2b333b;
}
.shopcart .btn.enough {
  background: #00b43c;
  color: #fff;
}
.shopcart .cart {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  z-index: -1;
  transition: all 0.5s;
  -webkit-transform: translate3d(0, -100%, 0);
  transform: translate3d(0, -100%, 0);
}
.shopcart .cart .cart-top {
  height: 40px;
  padding: 0 18px;
  line-height: 40px;
  background: #f3f5f7;
  position: relative;
}
.shopcart .cart .cart-top:after {
  display: block;
  position: absolute;
  left: 0;
  bottom: 0;
  border-top: 1px solid rgba(7, 17, 27, 0.1);
  content: ' ';
  width: 100%;
}
.shopcart .cart .cart-top .title {
  font-size: 14px;
  font-weight: 200;
  color: #07111b;
  line-height: 40px;
}
.shopcart .cart .cart-top .clear-btn {
  font-size: 14px;
  color: #00a0dc;
  float: right;
}
.shopcart .cart-content {
  padding: 0 18px;
  background: #fff;
  max-height: 217px;
  overflow: hidden;
}
.shopcart .cart-content .content-inner {
  color: #07111b;
}
.shopcart .cart-content .content-inner .price {
  position: absolute;
  right: 100px;
  top: 12px;
  color: red;
  line-height: 24px;
}
.shopcart .cart-content .content-inner .price .icon {
  display: inline-block;
  font-size: 10px;
  line-height: 24px;
}
.shopcart .cart-content .content-inner .price .price-num {
  display: inline-block;
  font-size: 14px;
  line-height: 24px;
  font-weight: bold;
}
.shopcart .cart-content .content-inner .good {
  padding: 12px 0;
  height: 24px;
  line-height: 24px;
  position: relative;
}
.shopcart .cart-content .content-inner .good:after {
  display: block;
  position: absolute;
  left: 0;
  bottom: 0;
  border-top: 1px solid rgba(7, 17, 27, 0.1);
  content: ' ';
  width: 100%;
}
.shopcart .cart-content .content-inner .shop-control {
  position: absolute;
  right: 0;
  top: 12px;
}
.shopcart .ball-container .ball {
  position: fixed;
  left: 32px;
  bottom: 22px;
  z-index: 200;
  transition: all 0.4s cubic-bezier(1, -0.05, 0.83, 0.67);
}
.shopcart .ball-container .ball .inner {
  width: 16px;
  height: 16px;
  border-radius: 100%;
  background: #00a0dc;
  transition: all 0.4s cubic-bezier(1, -0.05, 0.83, 0.67);
}



