.close {
  font-family: "iconfont";
  font-size: 24px;
  font-style: normal;
}
.decrease {
  font-family: "iconfont";
  font-size: 24px;
  font-style: normal;
  color: #00a0dc;
}
.increase {
  font-family: "iconfont";
  font-size: 24px;
  font-style: normal;
  color: #00a0dc;
}
.tab {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  height: 40px;
  line-height: 40px;
  position: relative;
}
.tab:after {
  display: block;
  position: absolute;
  left: 0;
  bottom: 0;
  border-top: 1px solid #eee;
  content: ' ';
  width: 100%;
}
.tab .tab-item {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  text-align: center;
  color: red;
  font-size: 14px;
}
.tab .tab-item > a {
  color: blue;
}
.tab .tab-item > a.active {
  color: red;
}



