.goods-wrapper {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  position: absolute;
  top: 174px;
  bottom: 46px;
  width: 100%;
  overflow: hidden;
}
.goods-wrapper .goods-type {
  -webkit-box-flex: 0;
      -ms-flex: 0 0 80px;
          flex: 0 0 80px;
  width: 80px;
  background: #f3f5f7;
}
.goods-wrapper .goods-type .type-item {
  padding: 0 12px;
  display: table;
  width: 56px;
  height: 54px;
  line-height: 14px;
  position: relative;
}
.goods-wrapper .goods-type .type-item:after {
  display: block;
  position: absolute;
  left: 0;
  bottom: 0;
  border-top: 1px solid #eee;
  content: ' ';
  width: 100%;
}
.goods-wrapper .goods-type .type-item.active {
  background: #999;
}
.goods-wrapper .goods-type .type-item .text {
  display: table-cell;
  vertical-align: middle;
  color: #000;
  font-size: 12px;
}
.goods-wrapper .goods-content {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  /*overflow-y:scroll;*/
}
.goods-wrapper .goods-content .good .good-title {
  border-left: 2px solid #d9dde1;
  padding-left: 14px;
  font-size: 12px;
  height: 26px;
  line-height: 26px;
  color: #93999f;
  background: #f3f5f7;
}
.goods-wrapper .goods-content .good .good-item {
  padding: 0 18px;
}
.goods-wrapper .goods-content .good .good-item .item-inner {
  position: relative;
  padding: 20px 0;
  font-size: 0;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
.goods-wrapper .goods-content .good .good-item .item-inner:after {
  display: block;
  position: absolute;
  left: 0;
  bottom: 0;
  border-top: 1px solid #ccc;
  content: ' ';
  width: 100%;
}
.goods-wrapper .goods-content .good .good-item .item-inner .good-img {
  margin-right: 4px;
}
.goods-wrapper .goods-content .good .good-item .item-inner .good-img img {
  vertical-align: top;
}
.goods-wrapper .goods-content .good .good-item .item-inner .good-text {
  display: inline-block;
  font-size: 12px;
  color: #07111b;
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
}
.goods-wrapper .goods-content .good .good-item .item-inner .good-text p {
  width: 100%;
}
.goods-wrapper .goods-content .good .good-item .item-inner .good-text .name {
  line-height: 14px;
  margin-top: 4px;
}
.goods-wrapper .goods-content .good .good-item .item-inner .good-text .description {
