}
.goods-wrapper .goods-content .good .good-item .item-inner .good-text .price .nowPrice {
  font-size: 20px／28px;
  color: red;
  font-weight: bold;
  margin-right: 8px;
}
.goods-wrapper .goods-content .good .good-item .item-inner .good-text .price .nowPrice .icon {
  font-size: 28px／28px;
  color: red;
}
.goods-wrapper .goods-content .good .good-item:last-child {
  border: none;
}
.goods-wrapper .list-mask {
  position: fixed;
  top: 0;
  left: 0;
