
.shop-control {
  float: right;
  margin-right: 18px;
}
.shop-control .increase-outer {
  display: inline-block;
  transition: all .4s linear;
  -webkit-transform: translate3D(0, 0, 0);
          transform: translate3D(0, 0, 0);
  /*&.move-enter-active,&.move-leave{*/
  /*opacity:1;*/
  /*transform:translate3D(0,0,0);*/
  /*.increase{*/
  /*&.move-enter-ative,&.move-leave{*/
  /*transform:rotate(0deg);*/
  /*}*/
  /*}*/
  /*}*/
}
.shop-control .increase-outer.move-enter,
.shop-control .increase-outer.move-leave-active {
  opacity: 0;
  -webkit-transform: translate3D(24px, 0, 0);
          transform: translate3D(24px, 0, 0);
}
.shop-control .increase-outer.move-enter .increase,
.shop-control .increase-outer.move-leave-active .increase {
  transition: all .4s linear;
  -webkit-transform: rotate(180deg);
          transform: rotate(180deg);
}
.shop-control .increase-outer .increase {
  display: inline-block;
  transition: all 0.4s linear;
  -webkit-transform: rotate(0deg);
          transform: rotate(0deg);
}
.shop-control .num {
  width: 24px;
  transition: all 0.4s;
}
.shop-control .num.fade-enter,
.shop-control .num.fade-leave-active {
  opacity: 0;
}
.shop-control .num.fade-leave,
.shop-control .num.fade-enter-active {
  opacity: 1;
}



